import { createSlice } from "@reduxjs/toolkit";
import { toast } from "react-toastify";


const cartSlice = createSlice({
    name: 'cart',
    initialState: {
        cartArr: JSON.parse(localStorage.getItem("cartArr")) || [],
        cartTotalQuantity: 0,
        cartTotalAmount: 0,
    },
    reducers: {
        //Thm sn phm
        ADD_CART(state, action) {
            //Ly id ca sn phm
            const itemIndex = state.cartArr.findIndex(
                (item) => item._id.$oid === action.payload._id.$oid
            );
            //Kim tra id ca sn phm nu  c th s cng quantity thm 1
            if(itemIndex >= 0) {
                state.cartArr[itemIndex].quantity += 1;
                toast.info(`increased ${state.cartArr[itemIndex].name} quantity`, 
                {
                    position: toast.POSITION.BOTTOM_LEFT
                }
            );
            } else {
                const tempProduct = { ...action.payload, quantity : 1};
                state.cartArr.push(tempProduct); // thm d liu vo mng cartArr
                //Hin th thng bo cho ngi dng
                toast.success(`${action.payload.name} added to cart`, {
                    position: "bottom-left"
                });
            }
            //Lu d liu vo local
            localStorage.setItem("cartArr", JSON.stringify(state.cartArr));
        },

        //Gim s lng sn phm
        DECREASE_CART(state, action) {
            //Ly id ca sn phm
            const itemIndex = state.cartArr.findIndex(
                item => item._id.$oid === action.payload._id.$oid
            )
            //Kim tra s lng ca sn phm nu > 1 th s tr 1, bng 1 th s xa sn phm 
            if(state.cartArr[itemIndex].quantity > 1) {
                state.cartArr[itemIndex].quantity -= 1
                //Hin th thng bo cho ngi dng
                toast.info(`Decreased ${action.payload.name} cart quantity`, {
                    position: "bottom-left"
                });

            } else if (state.cartArr[itemIndex].quantity === 1) {
                const nextCartItems = state.cartArr.filter(
                    cartItem => cartItem._id.$oid !== action.payload._id.$oid
                )
    
                state.cartArr = nextCartItems;
                
                toast.error(`${action.payload.name} removed from cart`, {
                    position: "bottom-left"
                });

                localStorage.setItem('cartArr', JSON.stringify(state.cartArr));
            }
        },

        //Xa sn phm
        DELETE_CART(state, action) {
            //Dng filter  ly id ca sn phm khi ngi dng mun xa
            const nextCartItems = state.cartArr.filter(
                cartItem => cartItem._id.$oid !== action.payload._id.$oid
            )
            //d liu mi s c lu li
            state.cartArr = nextCartItems;
            //Lu d liu vo local
            localStorage.setItem('cartArr', JSON.stringify(state.cartArr));
            //Hin th thng bo cho ngi dng
            toast.error(`${action.payload.name} removed from cart`, {
                position: "bottom-left"
            });
        },

        //hm ly tng s lng v gi tin
        getTotals(state, action) {
            //s dng hm reduce  ly tng 2 gi tr total v quantity 
            let {total, quantity } = state.cartArr.reduce((cartTotal, cartItem) => {
                const { price, quantity } = cartItem;
                const itemTotal = price * quantity;

                cartTotal.total += itemTotal
                cartTotal.quantity += quantity

                return cartTotal
            }, {
                //Gi tr khi to
                total: 0,
                quantity: 0
            });

            //gn 2 gi tr va ly vo initialState
            state.cartTotalQuantity = quantity;
            state.cartTotalAmount = total;
        },
    }
})

export const cartActions = cartSlice.actions;

export default cartSlice.reducer;